<template>
  <div class="heart-disease-predict">
    <h1>Submit Information for Heart Disease Prediction</h1>

    <div class="instructions">
      <h2>How to Use the Model</h2>
      <ol>
        <li>Input your weight in kg and height in meters (ex. 1.70) to calculate your BMI.</li>
        <li>Select age categories.</li>
        <li>Indicate whether you drink alcohol.</li>
        <li>Indicate if you have diabetes, kidney disease, or have difficulty walking.</li>
        <li>Enter your physical health score (0-5).</li>
        <li>Select your sex and smoking status.</li>
        <li>Enter your weekly physical activity hours.</li>
        <li>Click on the "Submit for Prediction" button.</li>
        <li>View the prediction result displayed below.</li>
        <li>Click on the "Make Another Prediction" button if you want to analyze new data.</li>
      </ol>
    </div>

    <h3>BMI Calculation</h3>
    <input type="number" v-model="weight" placeholder="Weight (kg)" min="0" required />
    <input type="number" v-model="height" placeholder="Height (m)" min="0" required />
    
    <div v-if="bmi !== null" class="bmi-display">
      <p>BMI: {{ bmi.toFixed(2) }}</p>
      <p>Classification: {{ bmiCategory }}</p>
    </div>

    <!-- Campos de entrada -->
    <select v-model="age" required>
      <option value="">Select Age Group</option>
      <option value="age_18-34">Age 18-34</option>
      <option value="age_35-49">Age 35-49</option>
      <option value="age_50-64">Age 50-64</option>
      <option value="age_65-100">Age 65-100</option>
    </select>

    <select v-model="alcoholdrinking" required>
      <option value="">Select Alcohol Drinking</option>
      <option value="yes">Yes</option>
      <option value="no">No</option>
    </select>

    <select v-model="diabetic" required>
      <option value="">Select Diabetic Status</option>
      <option value="yes">Yes</option>
      <option value="no">No</option>
    </select>

    <select v-model="kidneydisease" required>
      <option value="">Select Kidney Disease Status</option>
      <option value="yes">Yes</option>
      <option value="no">No</option>
    </select>

    <select v-model="diffwalking" required>
      <option value="">Select Difficulty Walking</option>
      <option value="yes">Yes</option>
      <option value="no">No</option>
    </select>

    <input type="number" v-model="physicalhealth" placeholder="Physical Health (0-5)" min="0" max="5" required />

    <select v-model="sex" required>
      <option value="">Select Sex</option>
      <option value="male">Male</option>
      <option value="female">Female</option>
    </select>

    <select v-model="smoking" required>
      <option value="">Select Smoking Status</option>
      <option value="yes">Yes</option>
      <option value="no">No</option>
    </select>

    <input type="number" v-model="weeklyPhysicalActivity" placeholder="Weekly Physical Activity (hours)" min="0" required />

    <!-- Botões para enviar os dados -->
    <button @click="submitData" :disabled="!isFormValid">Submit for Prediction</button>
    <button v-if="prediction" @click="resetForm">Make Another Prediction</button>

    <!-- Exibir ícone de loading -->
    <div v-if="loading" class="loading-icon-overlay">
      <div class="loading-icon">
        <img src="@/assets/hourglass.png" alt="Processing..." />
        <p>Processing your request, please wait...</p>
      </div>
    </div>

    <!-- Exibir resultado da previsão -->
    <div v-if="prediction" :class="{'high-risk': prediction === 'Heart Disease', 'low-risk': prediction === 'No Heart Disease'}">
      <h2>Prediction Result</h2>
      <p>{{ prediction }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HeartDiseasePredict',
  data() {
    return {
      age: '',
      alcoholdrinking: '',
      weight: null,
      height: null,
      bmi: null,
      bmiCategory: null,
      diabetic: '',
      kidneydisease: '',
      diffwalking: '',
      physicalhealth: null,
      sex: '',
      smoking: '',
      weeklyPhysicalActivity: null,
      prediction: null,
      loading: false,
    };
  },
  computed: {
    isFormValid() {
      return (
        this.age &&
        this.alcoholdrinking &&
        this.weight &&
        this.height &&
        this.diabetic &&
        this.kidneydisease &&
        this.diffwalking &&
        this.physicalhealth !== null &&
        this.sex &&
        this.smoking &&
        this.weeklyPhysicalActivity !== null
      );
    }
  },
  watch: {
    weight: 'calculateBMI',
    height: 'calculateBMI',
  },
  methods: {
    calculateBMI() {
      if (this.weight && this.height) {
        this.bmi = this.weight / (this.height * this.height);
        this.classifyBMI();
      } else {
        this.bmi = null;
        this.bmiCategory = null;
      }
    },
    classifyBMI() {
      const bins = [0, 18.5, 24.9, 29.9, 34.9, 39.9, 100];
      const labels = ['Underweight', 'Normal weight', 'Overweight', 'Obesity Class I', 'Obesity Class II', 'Obesity Class III'];

      for (let i = 0; i < bins.length - 1; i++) {
        if (this.bmi >= bins[i] && this.bmi < bins[i + 1]) {
          this.bmiCategory = labels[i];
          break;
        }
      }
    },
    async submitData() {
      this.loading = true;

      try {
        const requestData = {
          'age_18-34': this.age === 'age_18-34',
          'age_35-49': this.age === 'age_35-49',
          'age_50-64': this.age === 'age_50-64',
          'age_65-100': this.age === 'age_65-100',
          'alcoholdrinking_yes': this.alcoholdrinking === 'yes',
          'bmi_normal_weight': this.bmiCategory === 'Normal weight',
          'bmi_obesity_class_i': this.bmiCategory === 'Obesity Class I',
          'bmi_obesity_class_ii': this.bmiCategory === 'Obesity Class II',
          'bmi_obesity_class_iii': this.bmiCategory === 'Obesity Class III',
          'bmi_overweight': this.bmiCategory === 'Overweight',
          'diabetic_yes': this.diabetic === 'yes',
          'kidneydisease_yes': this.kidneydisease === 'yes',
          'diffwalking_yes': this.diffwalking === 'yes',
          'physicalhealth': this.physicalhealth,
          'sex_male': this.sex === 'male',
          'smoking_yes': this.smoking === 'yes',
          'weeklyPhysicalActivity': this.weeklyPhysicalActivity,
        };

        const response = await fetch('https://my-personal-site-73xl.onrender.com/predictheartdisease', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(requestData),
        });

        if (!response.ok) {
          const errorDetails = await response.text();
          throw new Error(`Error: ${response.status} ${response.statusText} - ${errorDetails}`);
        }

        const data = await response.json();
        console.log('Dados recebidos do backend:', data); // Log para verificar os dados
        this.prediction = data.heart_prediction; // Certifique-se de que esses nomes estão corretos

      } catch (error) {
        console.error('Error during request:', error.message);
        alert('An error occurred while fetching the prediction: ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    resetForm() {
      this.age = '';
      this.alcoholdrinking = '';
      this.weight = null;
      this.height = null;
      this.bmi = null;
      this.bmiCategory = null;
      this.diabetic = '';
      this.kidneydisease = '';
      this.diffwalking = '';
      this.physicalhealth = null;
      this.sex = '';
      this.smoking = '';
      this.weeklyPhysicalActivity = null;
      this.prediction = null;
    }
  }
};
</script>

