<template>
  <div class="skin-ai-predict">
    <h1>Submit Information for Prediction</h1>

    <div class="instructions">
      <h2>How to Use the Model</h2>
      <ol>
        <li>Select an example type.</li>
        <li>Enter age in the age input field.</li>
        <li>Select sex from the dropdown menu.</li>
        <li>Select the anatomical site of the lesion from the dropdown menu.</li>
        <li>Click on the "Submit for Prediction" button.</li>
        <li>View the prediction result and interpretation displayed below.</li>
        <li>Click on the "Make Another Prediction" button if you want to analyze a new lesion.</li>
      </ol>
    </div>

    <!-- Dropdown para selecionar o tipo de lesão -->
    <select v-model="selectedImage" required>
      <option value="">Select an Example Type</option>
      <option value="melanoma">Lesion 1</option>
      <option value="nevus">Lesion 2</option>
    </select>

    <!-- Exibir a imagem correspondente -->
    <div v-if="imageUrl" class="image-preview">
      <h3>Selected Image:</h3>
      <img :src="imageUrl" alt="Selected Skin Lesion" />
    </div>

    <!-- Outros campos de entrada -->
    <input type="number" v-model="age" placeholder="Age" required />
    <select v-model="sex" required>
      <option value="">Select Sex</option>
      <option value="male">Male</option>
      <option value="female">Female</option>
    </select>
    <select v-model="anatomicalSite" required>
      <option value="">Select Anatomical Site</option>
      <option value="anterior torso">Anterior Torso</option>
      <option value="head/neck">Head/Neck</option>
      <option value="lateral torso">Lateral Torso</option>
      <option value="lower extremity">Lower Extremity</option>
      <option value="oral/genital">Oral/Genital</option>
      <option value="palms/soles">Palms/Soles</option>
      <option value="posterior torso">Posterior Torso</option>
      <option value="upper extremity">Upper Extremity</option>
    </select>

    <!-- Botão para enviar os dados -->
    <button @click="submitData" :disabled="!selectedImage || !age || !sex || !anatomicalSite">Submit for Prediction</button>
    <button v-if="prediction" @click="resetForm">Make Another Prediction</button>

    <!-- Exibir ícone de loading -->
    <div v-if="loading" class="loading-icon-overlay">
      <div class="loading-icon">
        <img src="@/assets/hourglass.png" alt="Processing..." />
        <p>Processing your request, please wait...</p>
      </div>
    </div>

    <!-- Exibir o resultado da previsão -->
    <div v-if="prediction" :class="{'suspicious': prediction === 'Suspicious lesion', 'benign': prediction === 'Benign'}">
      <h2>Prediction Result</h2>
      <p>{{ prediction }}</p>
      <!-- Garantir que a interpretação fique sempre preta -->
      <p class="interpretation"><strong>Interpretation:</strong> {{ interpretation }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SkinAIPredict',
  data() {
    return {
      selectedImage: '',  // "melanoma" ou "nevus"
      imageUrl: null,  // URL da imagem carregada (apenas para visualização)
      prediction: null,
      interpretation: null,
      age: null,
      sex: '',
      anatomicalSite: '',
      loading: false,  // Adicionando estado de loading
    };
  },
  watch: {
    // Atualiza a imagem sempre que o usuário seleciona um tipo diferente
    selectedImage(newValue) {
      if (newValue === 'melanoma') {
        this.imageUrl = require('@/assets/melanoma.jpeg');  // Substitua pelo caminho correto
      } else if (newValue === 'nevus') {
        this.imageUrl = require('@/assets/nevus.jpeg');  // Substitua pelo caminho correto
      } else {
        this.imageUrl = null;
      }
    }
  },
  methods: {
    async submitData() {
      this.loading = true;  // Exibe o ícone de loading

      try {
        // Simula um delay de 2 segundos
        await new Promise(resolve => setTimeout(resolve, 2000));

        // Envia os dados ao backend
        const response = await fetch('https://my-personal-site-73xl.onrender.com/predict', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            lesion_type: this.selectedImage,  // "melanoma" ou "nevus"
            age: this.age,
            sex: this.sex,
            anatomical_site: this.anatomicalSite,
          }),
        });

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(`Error: ${response.statusText} - ${errorData.error || 'Unknown error'}`);
        }

        const data = await response.json();
        this.prediction = data.prediction;
        this.interpretation = data.interpretation;

      } catch (error) {
        console.error('Error:', error);
        alert(`An error occurred: ${error.message}`);
      } finally {
        this.loading = false;  // Oculta o ícone de loading após a resposta
      }
    },
    resetForm() {
      this.selectedImage = '';
      this.imageUrl = null;
      this.prediction = null;
      this.interpretation = null;
      this.age = null;
      this.sex = '';
      this.anatomicalSite = '';
    },
  },
};
</script>

