    <h2>Why I Didn't Remove Outliers</h2>
    <p>
      In public health data, outliers often reflect critical real-world events rather than errors or anomalies. Removing them could obscure valuable insights. Here’s why I chose to keep them:
    </p>
    <ul>
      <li>
