    <h2>🚨 Hypothesis Testing - Ischemic Stroke Mortality Rate</h2>
    <p>
      After verifying that the data did not follow a normal distribution, using both histogram visualization and the Shapiro-Wilk test, I applied the Kruskal-Wallis test to assess statistical differences between the groups (months). The test indicated significant differences between the analyzed months.
    </p>
    <p>
      As the next step, a Dunn post-hoc test was conducted, identifying which month pairs showed statistically significant differences. The results revealed:
    </p>
    <ul>
      <li>Significant differences between month 2 and months 9 and 10.</li>
      <li>Significant differences between month 3 and months 9, 10, and 11.</li>
    </ul>
    <p>
      These findings suggest that months 2 and 3 behave differently from months 9, 10, and 11, indicating that factors such as seasonality may influence ischemic stroke mortality rates during certain periods.
    </p>

